import React from "react";
import why from '../why.jpg'
function Whyus() {
  return (
    <React.Fragment>
      <div className="uk-container">
        <div className="uk-grid-margin uk-grid uk-grid-stack" uk-grid="true">
          <div className="uk-flex-auto uk-width-1-1@m uk-first-column">
            <h2
              className="uk-h2 uk-text-bold uk-text-center uk-margin-large-top uk-scrollspy-inview uk-animation-slide-top-medium"
              uk-scrollspy-classname="true"
            >
              About Dehnovo
            </h2>
            <div
              className="uk-divider-icon uk-width-medium uk-margin-auto uk-scrollspy-inview uk-animation-slide-top-medium"
              uk-scrollspy-classname="true"
            ></div>
          </div>
        </div>
        <div className="uk-grid-large uk-margin-large uk-grid" uk-grid="true">
          <div className="uk-grid-item-match uk-flex-middle uk-width-expand@m uk-first-column">
            <div className="uk-panel uk-width-1-1">
              <h2
                className="uk-h3 uk-margin-small uk-text-left@m uk-text-center uk-scrollspy-inview uk-animation-slide-left-medium"
                uk-scrollspy-classname="uk-animation-slide-left-medium"
              >
                About Dehnovo
              </h2>
              <div
                className="uk-text-primary uk-margin-small uk-text-left@m uk-text-center uk-scrollspy-inview uk-animation-slide-top-medium"
                uk-scrollspy-classname="true"
              >
                We are basically a Business to Business sales platform helping
                the new as well as existing businesses to grow their market by
                the corporate reach. We also help the corporate sector to
                fulfill all their needs at a single platform.
              </div>
              <div
                className="uk-margin-medium uk-text-left@m uk-text-center uk-scrollspy-inview uk-animation-slide-left-medium"
                uk-scrollspy-classname="uk-animation-slide-left-medium"
              >
                <h2
                  className="uk-h3 uk-margin-small uk-text-left@m uk-text-center uk-scrollspy-inview uk-animation-slide-left-medium"
                  uk-scrollspy-classname="uk-animation-slide-left-medium"
                >
                  Why Dehnovo
                </h2>
                Business mentors are key—that’s why when it comes to client
                selection, we’re choosy. We want to give each of you the time
                and guidance you deserve. We didn’t get there alone and neither
                will you. We prefer Quality over Quantity.
              </div>
            </div>
          </div>

          <div className="uk-grid-item-match uk-flex-middle uk-width-expand@m">
            <div className="uk-panel uk-width-1-1">
              <div
                uk-slideshow="minHeight: 410;"
                className="uk-margin uk-slideshow uk-scrollspy-inview uk-animation-slide-top-medium"
                uk-scrollspy-classname="true"
              >
                <div className="uk-position-relative">
                  <ul className="uk-slideshow-items">
                    <li
                      className="el-item uk-active uk-transition-active"
                      tabIndex="-1"
                    >
                      <img
                        className="el-image uk-cover"
                        alt="Why Dehnovo"
                        uk-img="target: !.uk-slideshow-items"
                        uk-cover="true"
                        data-src={why}
                        data-srcset={why}
                        data-sizes="(max-aspect-ratio: 920/613) 150vh"
                        data-width="920"
                        data-height="613"
                        src={why}
                        sizes="(max-aspect-ratio: 920/613) 150vh"
                        srcSet={why}
                      />
                    </li>
                  </ul>

                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
}

export default Whyus;
