import React, { useState, useEffect } from "react";
import axios from "axios";
import ProductCard from "./ProductCard";

import { isAuth, getCookie, signout, updateUser } from "../../auth/helper";
import { Link } from "react-router-dom";

function HomeDeals() {
  const [values, setValues] = useState([]);

  useEffect(() => {
    loadPosts();
  }, []);

  const loadPosts = () => {
    axios({
      method: "GET",
      url: `${process.env.REACT_APP_API}/post`,
    })
      .then((response) => {
        console.log("Posts", response.data);
        const value = response.data;

        const data = [];
        for (const val of Object.entries(value)) {
          data.push(val);
        }
        setValues(data);
      })
      .catch((error) => {
        console.log("Post error", error);
        if (error.response.status === 401) {
          console.log("error while retriving post");
        }
      });
  };

  return (
    <React.Fragment>
      <div className="uk-container uk-margin-large">
        {/* {JSON.stringify(values)} */}

        <h2 className="uk-text-center uk-text-bold">Product Deals</h2>
        <div
          className="uk-divider-icon uk-width-medium uk-margin-auto uk-scrollspy-inview uk-animation-slide-top-medium"
          uk-scrollspy-classname="true"
        ></div>
        <div className="uk-child-width-1-3@m" uk-grid="true">
          {values.slice(0,3).map((post) => (
            <ProductCard
              key={post[1]._id}
              id={post[1]._id}
              image={`${process.env.REACT_APP_API}/post/photo/${post[1]._id}`}
              title={post[1].title}
              description={post[1].description}
              postedBy={post[1].postedBy}
            />
          ))}
        </div>
        <div className="uk-text-center uk-margin">
          <Link
            // to={`/requirement/${post[1]._id}`}
            to="/requirements"
            className="uk-button uk-button-primary"
          >
            View More
          </Link>
        </div>
      </div>
    </React.Fragment>
  );
}

export default HomeDeals;
